import React, { SelectHTMLAttributes, useState, useRef, useEffect } from 'react'
import { ChevronDown } from 'lucide-react'
import { clsx } from 'clsx'

interface SelectProps extends Omit<SelectHTMLAttributes<HTMLSelectElement>, 'size'> {
  label?: string
  error?: string
  helperText?: string
  size?: 'sm' | 'md' | 'lg'
  variant?: 'default' | 'minimal'
  children: React.ReactNode
}

export function Select({
  label,
  error,
  helperText,
  size = 'md',
  variant = 'default',
  className,
  children,
  id,
  ...props
}: SelectProps) {
  const [isOpen, setIsOpen] = useState(false)
  const selectRef = useRef<HTMLSelectElement>(null)

  // Track when select is opened/closed
  useEffect(() => {
    const handleFocus = () => setIsOpen(true)
    const handleBlur = () => setIsOpen(false)
    const handleMouseDown = () => setIsOpen(true)
    const handleMouseUp = () => setIsOpen(false)

    const selectElement = selectRef.current
    if (selectElement) {
      selectElement.addEventListener('focus', handleFocus)
      selectElement.addEventListener('blur', handleBlur)
      selectElement.addEventListener('mousedown', handleMouseDown)
      selectElement.addEventListener('mouseup', handleMouseUp)

      return () => {
        selectElement.removeEventListener('focus', handleFocus)
        selectElement.removeEventListener('blur', handleBlur)
        selectElement.removeEventListener('mousedown', handleMouseDown)
        selectElement.removeEventListener('mouseup', handleMouseUp)
      }
    }
  }, [])

  const sizeClasses = {
    sm: 'px-3 py-2 text-sm',
    md: 'px-3 py-2 text-sm',
    lg: 'px-4 py-3 text-base'
  }

  const baseClasses = clsx(
    'block w-full border rounded-md shadow-sm transition-all duration-200',
    'bg-white text-gray-900',
    'focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500',
    'disabled:bg-gray-50 disabled:text-gray-500 disabled:cursor-not-allowed',
    // Enhanced styling for better visual consistency
    'min-h-[40px]',
    sizeClasses[size],
    variant === 'minimal' ? 'border-0 bg-transparent shadow-none' : 'border-gray-300 hover:border-gray-400',
    className
  )

  const errorClasses = error
    ? 'border-red-300 focus:ring-red-500 focus:border-red-500'
    : ''

  const finalClasses = clsx(baseClasses, errorClasses)

  // Custom dropdown indicator styling
  const selectWrapperClasses = clsx(
    'relative',
    variant !== 'minimal' && 'shadow-sm'
  )

  return (
    <div className="space-y-1">
      {label && (
        <label htmlFor={id} className="block text-sm font-medium text-gray-700">
          {label}
        </label>
      )}

      <div className={selectWrapperClasses}>
        <select
          ref={selectRef}
          id={id}
          className={finalClasses}
          {...props}
        >
          {children}
        </select>

        {/* Custom dropdown arrow */}
        <div className="absolute inset-y-0 right-0 flex items-center pr-3 pointer-events-none">
          <ChevronDown className={clsx(
            'h-5 w-5 text-gray-400 transition-transform duration-200',
            isOpen && 'transform rotate-180'
          )} />
        </div>
      </div>

      {error && (
        <p className="text-sm text-red-600">{error}</p>
      )}

      {helperText && !error && (
        <p className="text-sm text-gray-500">{helperText}</p>
      )}
    </div>
  )
}
